.clearfix::after {
  content: "";
  clear: both;
  display: table;
}

.skip-link {
  position: absolute;
  left: 50%;
  top: 2px;
  width: 1px;
  height: 1px;
  overflow: hidden;
  transform: translateX(-50%);
}
.skip-link:focus {
  position: fixed;
  width: auto;
  height: auto;
  padding-inline: 2rem;
  padding-block: 0.5rem;
}

.webp,
.temp-img {
  max-width: 100px;
  width: 90%;
  height: auto;
}

.skip-link:focus {
  z-index: 1000;
}

.navbar {
  z-index: 100;
}

.hero-text {
  z-index: 10;
}

.menu__tab-link::after {
  z-index: 1;
}

.confirmation-message__container {
  z-index: 400;
}

/* Slideshow container */
.carousel {
  position: relative;
  margin: auto;
  max-width: 31.25rem;
  height: 100%;
  overflow: hidden;
  /* // @include min-mq (xs) {
  //     margin-top: f.rem(45);
  // } */
}

/* Hide the images by default */
.carousel__slide {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  transition: left 1s ease;
}

/* Show the current slide */
.carousel__slide.current-slide {
  left: 0;
  z-index: 7;
}

/* Move the previous slide off-screen to the left */
.carousel__slide.previous-slide {
  left: -100%;
}

/* Move the next slide off-screen to the right */
.carousel__slide.next-slide {
  left: 100%;
}

/* Cover element to hide previous slides */
.carousel__cover {
  position: absolute;
  top: 0;
  left: 0;
  width: 81px;
  height: 600px;
  background: white;
  z-index: 4;
}

.carousel__image {
  display: block;
  margin-inline: auto;
  width: 100%;
  height: auto;
  border-radius: 5px 5px 0 0;
}

/* // carousel text */
.carousel__image-description {
  margin-inline: auto;
  /* // padding: f.rem(6) f.rem(12); */
  width: 85%;
  max-width: 350px;
  text-align: center;
  border-radius: 0 0 5px 5px;
}

/* //Next & previous buttons */
.carousel__prev-button,
.carousel__next-button {
  cursor: pointer;
  position: absolute;
  top: 45%;
  width: auto;
  font-weight: bold;
  font-size: 1rem;
  transition: 0.6s ease;
  user-select: none;
  border: 0;
  background-color: transparent;
  z-index: 163;
}

.carousel__prev-button {
  left: -4px;
}

.carousel__next-button {
  right: -4px;
}

