/* The dots indicators */
.carousel__bottom-control {
  margin-top: 600px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  z-index: 163;
}

.carousel__progress-list {
  display: flex;
  flex-direction: row;
  min-width: 150px;
}

.carousel__pause-button,
.carousel__play-button {
  border: none;
  background-color: transparent;
  display: block;
}

.carousel__pause-button.hidden,
.carousel__play-button.hidden {
  display: none;
}

.carousel__transition-button.disabled {
  pointer-events: none;
  opacity: 0.5;
}

.carousel__progress-container {
  display: flex;
  align-items: center;
  cursor: pointer;
  margin: 0 5px;
  padding: 0;
}

.carousel__progress-bar {
  margin: 0;
  padding: 0;
  height: 0.75rem;
  width: 0.75rem;
  border-radius: 100%;
  border: none;
  box-shadow: none;
  cursor: pointer;
}

.current-container {
  max-width: 3.5rem;
  width: 100%;
  height: 0.75rem;
  border-radius: 0.75rem;
}

.current-bar {
  width: 0%;
  border-radius: 0.75rem;
}

body {
  background-color: #F5F7F8;
  color: #707070;
  color: #304D30;
}

h1,
h2 {
  color: #163020;
}

h3,
h4,
h5,
h6 {
  color: #76885B;
  color: #707070;
}

button {
  display: block;
  width: 12.5rem;
  height: 3.125rem;
  background-color: #76885B;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
}

button a {
  text-decoration: none;
}

a {
  color: #76885B;
}

/* Caption text */
.carousel__image-description {
  background-color: #76885B;
  color: white;
}

.carousel__prev-button,
.carousel__next-button {
  color: #76885B;
}

.carousel__progress-container {
  background-color: transparent;
}

.current-container,
.carousel__progress-bar {
  background-color: #BFCCB5;
}

.carousel__progress-list:focus,
.current-bar:focus {
  outline: 2px solid #A7D397;
}

.current-bar,
.finished-bar {
  background-color: #76885B;
}

